.body-input-div{
    display:flex;
    margin-top: 10vh;
    border-radius: 50%;
    align-items: center;
    align-self: center;
    margin-left: auto;
    display: inline-flex;
    margin-right: auto ;
}
.body-input{
    border-top-left-radius: 30px;
    border: none;
    border-bottom-left-radius: 30px;
    height: 50px;
    text-align: center;
    padding: 5px;
    font-weight: 300;
}
.body-input:focus {
    outline: none !important;
    border:none;
}
.body-input-btn{
    width: 50px;
    height:50px;
    background: white;
    border: none;
    padding:10px;
    border-bottom-right-radius: 30px;
    border-top-right-radius: 30px;
    cursor: pointer;

}
.body-input-btn:focus {
    outline: none !important;
    border:none;
}

/* footer bnt */
.footer-btn{
    width: 10vh;
    height: 10vh;
    cursor: pointer;
    position: fixed;
    bottom: 5vh;
    left:5vh;
    background-color: #f0bd16;
    border-radius: 50%;
    padding: 20px;
    overflow: hidden;
    -webkit-transition: all 0.2s linear ;
    transition: all 0.2s linear ;
}
.footer-btn:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.footer-btn::before{
    content: "";
    position: absolute;
    width: 100%;
    top:0;
    left:0;
    height:100%;
    background-color: black;
    opacity: 0.0;
    -webkit-transition: all 0.2s linear ;
    transition: all 0.2s linear ;

}
.footer-btn:hover::before{
    opacity: 0.3;
}


