.sign-up-form-div{
    height: 100%;
    justify-content: center;
    /* background: rgb(18,51,6); */
    /* background: linear-gradient(180deg, rgba(18,51,6,1) 0%, rgba(77,105,196,1) 0%, rgba(91,67,240,1) 0%, rgba(104,55,163,0.9640231092436975) 49%, rgba(118,40,168,1) 72%, rgba(106,4,171,1) 89%); */

