}
.sign-up-form input[type=submit]{
   
    color: #fff;
    background-color: #6e00ff;
    border-color: #6e00ff;
    box-shadow: inset 0 1px 0 hsla(0,0%,100%,.15);
    display: inline-block;
    font-weight: 600;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    padding: .75rem 1.75rem;
    font-size: 1rem;
    line-height: 1.5;
    cursor: pointer;
    border-radius: .25rem;
  
}
.sign-up-form p{
