    font-size: 0.6rem;
    color:gray;
}

/* Side Drawer */

.MuiList-root {
    background-color: #162e4d;
    /* transform: translateX(-200px); */

}
.dropdown-header {
    display: block;
    padding: .35rem 1rem;
    margin-bottom: 0;
    font-size: .875rem;
    color: #95aac9;
    white-space: nowrap;
}
.dropdown-list-item {
    display: flex;
    width: 100%;
    padding: .25rem 1rem;
    clear: both;
    font-weight: 400;
    color: #6e84a3;
    text-align: inherit;
    white-space: nowrap;
    background-color: transparent;
    border: 0;
    position: relative;
    cursor: pointer; 
    -webkit-transition: all 0.2s ; 
    transition: all 0.2s ;

}
.dropdown-list-item img{
    width: 20px;
    justify-self: center;
    margin-top: 0;
    margin-right: 10px;
    height: 20px;
}
.dropdown-list-item :hover{
    color:white;
}

